@charset "UTF-8";
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'NotoSansR', '맑은 고딕', sans-serif;
  font-weight: normal;
  color: white;
  z-index: 1;

  ::-webkit-scrollbar {
    display: none;
  }
  scrollbar-width: none;
}

ul,
dl,
li,
ol {
  list-style: none;
}

h1,
h2,
h3,
h4,
h5 {
  font-size: 24px;
}

button {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;
}

select {
  outline: none;
}

input {
  border: none;
  outline: none;
}

table {
  border-collapse: collapse;
}

a {
  text-decoration: none;
}